import React,{useEffect, useState} from 'react';
import {Link} from 'react-router-dom';
export default function TestInfo ({users}) {
  
      return (
    <div className ="container-Ti">
       <div>
        <h2 className ="testItem-Header">Below is a list of JSON Placeholder APIS</h2>
        <p className ='testItem-p'>click any item to be redirected to a more detailed page of the selected item.<br/> (the URL will dynamically change as well)</p>
        {users.map(user => {
                     
               return(
                        <div key={user.id}>
                       <Link to ={`/test/${user.name}`}>
                        <h2>{user.name}</h2>
                        </Link>
                        </div>
                )})}
        </div>
    </div>
    );
}