import React, {useReducer, useState} from 'react';
import {Validate} from './Validate.js';


function formReducer (state, action){
    switch(action.type){
        case "submit":{
            return null;
        };
        case "field":{
            return{
            ...state,
            [action.field]: action.payload,
        };
            }
            case "loading":{
            return {
                ...state,
                error:" ",
                isLoading:true,   
                   };
            }
            case "loaded":{
            return{
                ...state,
                isLoading:false,
            };
                }
        case "error":{
            return{
                ...state,
                error:"*Please enter your name / valid email"
            };
        }
        
        case "success":{
            return{
                firstName:"",
                lastName:"",
                phone:"",
                email:"",
               error:"Success! However, as this is just a practice PHP form hosted on Netlify there is no PHP runtime on the Netlify CDN :("
            }
        }  
    };
}

const initialState = {
    firstName:"",
    lastName:"",
    phone:"",
    email:"",
    error:"",
    isLoading:false,
}

export default function Contact () {
    const [state, dispatch] =useReducer(formReducer, initialState)
    const {firstName, lastName, phone, error, email, isLoading} = state;
    
const contact ={
    paddingBottom:"1rem"

}
const errorStyle ={
    color:"red",
    fontWeight:"bold",
    paddingBottom:"1rem",
}
    const handleSubmit = async (e) => {
        e.preventDefault();
        dispatch({type:"loading"});
    try{
        await Validate({state});
        dispatch({type:'success'})
        } catch (error){
            dispatch({type:"error"});
        }
        dispatch({type:"loaded"});
    }
    //client side form validation, HTML form validation, and JS for validation examples provided 
    
    return (
    <div className ="form-items">
        <h1 style ={contact}> Contact üìù</h1>
        <p style = {errorStyle}>{error}</p>
        <form onSubmit = {handleSubmit} action = {"./contactForm.php"} method={'POST'}>
            <div className ="form-container">
            <input
            value={firstName}
            name ="firstName"
            placeholder =" "
            autoComplete ="no"
            required = {true}
            disabled ={isLoading}
            onChange = {e => 
                dispatch({
                       type: 'field',
                       field: 'firstName',
                       payload: e.currentTarget.value,
                        })
                    }
            />
            <label className="form-label" htmlFor= "firstName"><span className ="label-push">First Name</span></label>
            </div>
            <div className ="form-container">
             <input
             value={lastName}
            name= "lastName"
            placeholder =" "
            autoComplete ="no"
            required = {true}
            disabled ={isLoading}
            onChange = {e => 
                dispatch({
                       type: 'field',
                       field: 'lastName',
                       payload: e.currentTarget.value,
                        })
                    }
            />
                <label htmlFor= "lastName" className="form-label"><span className = "label-push">Last Name</span></label>
            </div>
            <div className ="form-container">
             <input
            value={phone}
            name= "phone"
            type="tel"
            placeholder =" "
            autoComplete ="new-name"
            disabled ={isLoading}
            onChange = {e => 
                dispatch({
                       type: 'field',
                       field: 'phone',
                       payload: e.currentTarget.value,
                        })
                    }
            />
            <label htmlFor= "phone" className="form-label"><span className ="label-push">Phone Number (optional)</span></label>
            </div>
            <div className ="form-container">
                <input
                value={email}
                name ="email"
                type="email"
                placeholder = " "
                autoComplete ="no"
                required = {true}
                disabled ={isLoading}            
                onChange = {e =>
                dispatch({
                type:"field",
                field:"email",
                payload: e.currentTarget.value,
                        })
                    }
                />
                <label htmlFor= "email" className="form-label"><span className = "label-push">Email</span></label>
                
            </div>
            <button className= "submit-btn" disabled ={isLoading}>{isLoading ? "Submitting..." : "Submit"}</button>
        </form>
    </div>
    );
}