import React from 'react';

export async function Validate ({state}) {
    return new Promise((resolve, reject) => {
       setTimeout(() => {
        if(state.email.includes("@") && state.firstName.trim() && state.lastName.trim()){
           resolve();
           } else{
                  reject();
                  }   
           
    
    }, 1000);
    
 });
}
                       


